<template>
  <div style="width:100%; height:100%; display:block; background:#fff;">
    <mobile-header
      title="회원가입"
      class="join-header"
      right-icon="mdi-help-circle-outline"
      @close="closeJoinPage"
    />
    <div>
      <div id="m_join" v-if="step === 'first'">
        <StepBox step="1" />

        <StepDesc desc1="본인확인을 위해" desc2=" 인증을 진행해 주세요" />

        <AgreeBox
          :service-agree="serviceAgree"
          @agreeService="serviceAgree = !serviceAgree"
        />

        <section>
          <div id="certify-number">
            <div>
              <v-text-field
                v-model="member.memberName"
                type="text"
                color="main"
                placeholder="이름을 입력해주세요"
              />
            </div>
            <div class="phone-box">
              <v-text-field
                v-model="member.memberPhone"
                type="text"
                color="main"
                placeholder="휴대폰 번호"
                maxlength="11"
                @input="changePhone"
                @keyup.enter="certifyTrans"
              >
                <template #append>
                  <button @click="certifyTrans">
                    {{ certifyTransFlag ? "다시요청" : "인증요청" }}
                  </button>
                </template></v-text-field
              >
            </div>
            <div v-if="certifyTransFlag" class="address">
              <!-- <div class="address"> -->
              <v-text-field
                v-model="certifyNumber"
                type="text"
                placeholder="인증번호 6자리"
                maxlength="6"
                color="main"
              >
                <template slot="append">
                  <p style="font-size: 14px" class="mb-0">{{ TimerStr }}</p>
                </template>
              </v-text-field>
            </div>
          </div>
        </section>

        <div
          id="next-btn__box"
          :class="{ active: certifyNumber && certifyNumber.length === 6 }"
        >
          <button @click="certifyCertify">
            다음
          </button>
        </div>
      </div>

      <div id="m_join" v-else-if="step === 'second'">
        <step-box step="2" />
        <StepDesc desc1="아이디와 비밀번호를" desc2="입력해 주세요" />

        <section class="id-pw__wraps">
          <v-text-field
            v-model="member.id"
            color="main"
            :rules="rules.id"
            placeholder="아이디(4 ~ 13자리 이내)"
            required
          />

          <v-text-field
            v-model="member.memberPw"
            color="main"
            :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
            :type="show1 ? 'text' : 'password'"
            :rules="rules.password"
            placeholder="비밀번호(10 ~ 20자리 이내)"
            required
            @click:append="show1 = !show1"
          />

          <v-text-field
            ref="passwordRe"
            v-model="memberPwRe"
            color="main"
            :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
            :type="show1 ? 'text' : 'password'"
            placeholder="비밀번호 확인"
            required
            @click:append="show1 = !show1"
          />
        </section>
        <div
          id="next-btn__box"
          :class="{ active: member.id && member.memberPw === memberPwRe }"
        >
          <button @click="validIdPw">
            다음
          </button>
        </div>
      </div>

      <div id="m_join" v-else-if="step === 'third'">
        <StepBox step="3" />
        <StepDesc desc1="상조 정보를 등록해 주세요" />
        <CorpList
          v-if="!corp"
          class="corp-list__mobile"
          :corp="corp"
          @selectCorp="selectCorp"
        />
        <section v-if="!notBelongCorp" class="id-pw__wraps">
          <div v-if="corp">
            <div class="corp-input__wrap">
              <h4>{{ corp.text }}</h4>
              <div class="certify">
                <h4 class="mb-0 mt-4">인증 비밀번호</h4>
                <v-text-field
                  v-model="corpPassword"
                  hide-details
                  dense
                  placeholder="초기 비밀번호 0000"
                  :append-icon="
                    show2 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'
                  "
                  :type="show2 ? 'text' : 'password'"
                  color="main"
                  @click:append="show2 = !show2"
                />
              </div>
              <div class="button__wraps" style="text-align:right">
                <button @click="certifyCorp">인증하기</button>
              </div>
            </div>

            <div class="retry-button__wraps">
              <button @click="retry">다시 선택하기</button>
            </div>
          </div>
        </section>

        <p class="corp-input-notice">
          상조에 따라 본 서비스 정책의 적용범위가 달라질 수 있습니다.
        </p>
        <div id="next-btn__box" class="active">
          <button @click="submitTerm">
            가입 완료하기
          </button>
        </div>
      </div>

      <div v-else-if="step === 'fourth'" id="m_join">
        <StepDesc
          :desc1="member.memberName + '님'"
          desc2="가입해주셔서 감사합니다"
        />

        <div id="next-btn__box" class="active">
          <button @click="routingAdminMain">
            로그인하러 가기
          </button>
        </div>
      </div>
    </div>
    <TermPopup v-if="agreeFlag" :method="submit" />
  </div>
</template>

<script>
import "@/assets/js/jquery-1.9.1.min.js";
import "@/assets/js/ux_common.js";
import myMixin from "@/mixin/mixin";
import rules from "@/mixin/rules";

import TermPopup from "@/components/common/TermPopup";
import MobileHeader from "@/components/common/MobileHeader.vue";
import StepBox from "@/components/common/StepBox.vue";
import StepDesc from "@/components/common/StepDesc.vue";
import AgreeBox from "@/components/common/AgreeBox.vue";
import CorpList from "../../components/common/CorpList.vue";
export default {
  name: "JoinSolo",
  mixins: [myMixin, rules],
  components: {
    MobileHeader,
    TermPopup,
    StepBox,
    StepDesc,
    AgreeBox,
    CorpList
  },
  data() {
    return {
      serviceAgree: false,
      masterFlag: false,
      corp: null,
      show1: false,
      corpPassword: "",
      member: {
        id: "",
        memberAddr: "",
        memberBirth: null,
        memberClass: "MEMBER",
        memberName: "",
        memberPhone: "",
        memberPush: null,
        memberPushYn: "Y",
        memberPw: "",
        memberType: "NORMAL",
        memberCorpMaster: "N",
        memberAttr: {
          codeId: "TYPE-1"
        },
        memberUseYN: {
          codeId: "SUCCESS-0"
        }, //솔로는 무조건 승인
        memberMarketingAgree: false,
        memberCorpCode: {
          codeId: ""
        }
      },
      memberPwRe: null,
      files: [],
      certifyBeforeNumber: "",
      certifyTransFlag: false,
      certifyCertifyFlag: false,
      certifyNumber: "",
      agreeFlag: false,
      searchCorp: null,

      Timer: null,
      TimeCounter: 180,
      TimerStr: "03:00",

      step: "first",
      notBelongCorp: false,
      show2: false,
      pwCheckFlag: false
    };
  },
  props: {},
  mounted() {
    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
    //document.head.appendChild(recaptchaScript)
  },
  methods: {
    closeJoinPage() {
      this.$router.go(-1);
    },

    changePhone() {
      this.certifyTransFlag = false;
      this.certifyBeforeNumber = "";
      this.certifyCertifyFlag = false;
    },
    certifyCertify() {
      if (!this.certifyNumber) return;

      if (this.certifyBeforeNumber + "" !== this.certifyNumber + "") {
        alert("인증번호가 일치하지 않습니다.");
      } else {
        this.certifyCertifyFlag = true;
        alert("인증되었습니다.");
        this.step = "second";
      }
    },
    certifyTrans() {
      if (!this.serviceAgree) return alert("서비스 약관을 동의해주세요");

      let result = Math.floor(Math.random() * 1000000) + 100000;
      if (result > 1000000) {
        result = result - 100000;
      }
      if (!this.phoneCheck(this.member.memberPhone)) {
        return;
      }

      this.certifyBeforeNumber = result;

      const parameter = {
        param: {
          phone: this.member.memberPhone,
          certifyNumber: this.certifyBeforeNumber
        }
      };
      this.$store.dispatch("REQUEST_MEMBER_CERTIFY", parameter).then(data => {
        if (data.result) {
          alert("인증번호를 전송하였습니다.");
          this.certifyTransFlag = true;

          if (this.Timer !== null) {
            this.timerStop(this.Timer);
            this.Timer = null;
          }
          this.Timer = this.timerStart();
        }
      });
    },
    validataion() {
      //if(!this.emailCheck(this.member.id)){
      //    return false;
      //}

      if (
        this.member.memberCorpCode.codeId === null ||
        this.member.memberCorpCode.codeId === undefined ||
        this.member.memberCorpCode.codeId === ""
      ) {
        alert("상조회사를 선택해 주세요.");
        return;
      }

      if (!this.pwCheckFlag) {
        alert("상조 인증을 진행해주십시오");
        return;
      }

      return true;
    },
    submitTerm() {
      var regType1 = /^[A-Za-z0-9+]*$/;
      if (!regType1.test(this.member.id)) {
        alert("아이디는 숫자와 영어만 입력 가능합니다.");
        return;
      }
      if (!this.validataion()) {
        return;
      }
      this.$store.commit("START_DEEMED");
      this.agreeFlag = true;
    },
    submit(val) {
      this.agreeFlag = false;
      this.$store.commit("END_DEEMED");
      if (!this.validataion()) {
        return;
      }

      if (this.masterFlag) {
        this.member.memberCorpMaster = "Y";
        this.member.memberUseYN.codeId = "SUCCESS-1"; //마스터 상주는 대기
      } else {
        this.member.memberCorpMaster = "N";
        this.member.memberUseYN.codeId = "SUCCESS-0"; //마스터 상주는 대기
      }

      this.member.memberMarketingAgree = val;
      const parameter = {
        member: this.member,
        files: this.files
      };
      this.$store.dispatch("REQUEST_JOIN", parameter);

      this.step = "fourth";
    },

    timerStart() {
      this.TimeCounter = 180;
      const interval = setInterval(() => {
        this.TimeCounter--;
        this.TimerStr = this.prettyTime();
        if (this.TimeCounter <= 0) this.timerStop(interval);
      }, 1000);
    },
    timerStop(timer) {
      clearInterval(timer);
      this.TimeCounter = 0;
    },
    prettyTime() {
      const time = this.TimeCounter / 60;
      const minutes = parseInt(time);
      const secondes = Math.round((time - minutes) * 60);
      return `${minutes
        .toString()
        .padStart(2, "0")} : ${secondes.toString().padStart(2, "0")}`;
    },
    validIdPw() {
      if (!this.member.id) return alert("아이디를 입력하여 주십시오");

      if (!this.member.memberPw) return alert("비밀번호를 입력하여 주십시오");

      if (this.member.memberPw !== this.memberPwRe) {
        return alert("비밀번호 확인이 일치하지 않습니다");
      }

      this.step = "third";
    },
    selectCorp(corp) {
      this.searchCorp = null;
      this.corp = corp;

      this.member.memberCorpCode.codeId = corp.value;
    },

    certifyCorp() {
      this.pwCheckFlag = false;
      for (let i = 0; i < this.getCodeList("CORP").length; i++) {
        if (
          this.getCodeList("CORP")[i].value ===
          this.member.memberCorpCode.codeId
        ) {
          if (this.getCodeList("CORP")[i].codeSubText === this.corpPassword) {
            this.pwCheckFlag = true;
            alert("인증 되었습니다");
          }
        }
      }
      if (!this.pwCheckFlag) {
        return alert("상조회사 비밀번호를 확인해 주세요.");
      }
    },

    retry() {
      this.corp = null;
      this.pwCheckFlag = false;
    },
    routingAdminMain() {
      this.$router.replace("/");
    }
  }
};
</script>
